"use strict";
boannA.controller('FlichtSimController', ['$scope', '$http', '$window', function($scope, $http, $window) {
        //config flight map        
        $scope.URI           = "../../libraries/callback/callback_msfs.php";        
        $scope.getZoom       = null;
        $scope.setZoom       = 8;
        $scope.refresh       = 35000;        
        $scope.refreshFlight = 1000;        
        $scope.flightRoute   = [];
        $scope.markers       = [];
        $scope.LatLng        = null;
        $scope.set_bounds    = true;
        $scope.flightInfo    = false;
        $scope.callsing      = "";


        //google map functions
        refreshPage();
        setInterval(refreshPage, $scope.refresh);

        //check flicht
        get_flights();
        setInterval(get_flights, $scope.refreshFlight);

        //refresh the functions what we need!
        function refreshPage(){
            for(var i=0; i < $scope.markers.length; i++){
                $scope.markers[i].setMap(null);
            }

            $scope.markers       = [];
            fetch_aircraft();            
        }

        function fetch_aircraft(){
            $scope.bounds  = new google.maps.LatLngBounds();
            
            $http.get($scope.URI, {params:{action:"get_flights_json"}}).then(function(data){
                if(data.status === 200){
                    $scope.flightData = data.data;
                    for (i = 0; i < $scope.flightData.length; i++) { 
                        /* 
                        $scope.gDate = new google.maps.LatLng($scope.flightData[i]["latitude"], $scope.flightData[i]["longitude"]);
                        $scope.flightRoute.push($scope.gDate); */

                        //create airplane as marker!
                        //$scope.LatLng = $scope.flightData[i]["latitude"] +', '+ $scope.flightData[i]["longitude"]; 
                        
                        $scope.marker = new google.maps.Marker({
                            map: $scope.map,
                            position: new google.maps.LatLng($scope.flightData[i]["latitude"], $scope.flightData[i]["longitude"]),
                            icon: {
                                path: "M21 16v-2l-8-5V3.5c0-.83-.67-1.5-1.5-1.5S10 2.67 10 3.5V9l-8 5v2l8-2.5V19l-2 1.5V22l3.5-1 3.5 1v-1.5L13 19v-5.5l8 2.5z",
                                fillColor: 'darkblue',
                                fillOpacity: 1,
                                anchor: new google.maps.Point(12, 12),
                                strokeWeight: 1,
                                scale: 1.5,
                                rotation: parseInt($scope.flightData[i]["heading"])
                            }
                        });

                        //if the user click on the plaine we do some nice stuff 
                        $scope.markers.push($scope.marker);
                        $scope.bounds.extend($scope.marker.getPosition());                        
                        google.maps.event.addListener($scope.marker, 'click', (function(marker, i) { return function() 
                            {      
                                $scope.flightInfo    = true;
                                
                                localStorage.setItem("tourplan", "showRoute");
                                localStorage.setItem("callsign", $scope.flightData[i].callsign);   

                                $("#flight-info-wrapper").fadeIn();  
                                $scope.flightRoute   = [];
                                tourplans();                                                          
                            }                            
                          })($scope.marker, i));
                        
                        
                            $scope.map.fitBounds($scope.bounds);
                            $scope.map.setZoom($scope.setZoom);                            
                            
                            tourplans();                                                                          
                        
                    }
                };
            });
        }       
        

        /* get GooleMaps */
        $scope.map = new google.maps.Map(document.getElementById('googlemap'), {
            disableDoubleClickZoom: true,
            draggable: true,
            scrollwheel: true,                        
            disableDefaultUI: true,            
            mapTypeId: google.maps.MapTypeId.TERRAIN
        });


        
        /* get flight information */
        function get_flights(){            
            if(localStorage.getItem("callsign") !== null || localStorage.getItem("callsign") !== "N/A"){
                $http.get($scope.URI, {params:{action:"callsign", callsing:localStorage.getItem("callsign")}}).then(function(data){
                    if(data.status = 200){
                        if(data.data){
                            $scope.flight = data.data;

                            //progressbar
                            $scope.totalDistance = $scope.flight.currentLocation.distance_remaining + $scope.flight.currentLocation.distance_flown;
                            $scope.progress      = $scope.flight.currentLocation.distance_flown / $scope.totalDistance;
                            $scope.progress =   $scope.progress = + $scope.progress.toFixed(2).split(".")[1];                                            
                            
                            if($scope.flightInfo = true){
                                $scope.gDate = new google.maps.LatLng($scope.flight.currentLocation.latitude, $scope.flight.currentLocation.longitude);
                                $scope.flightRoute.push($scope.gDate);
                            }
                        
                        }
                    };
                });
            }
        }
        
        /* close flight information */
        $scope.close    =   function(data){
            $scope.flightInfo = false;
            $("#flight-info-wrapper").fadeOut();
            if(data === localStorage.getItem("callsign")){
                localStorage.removeItem("callsign");            
                localStorage.removeItem("tourplan");
                $scope.flightRoute   = [];
                $scope.tourplan.setMap(null);
            }
        }

        /* draw the route in the flight */
        function tourplans(){
            if(localStorage.getItem("tourplan") === "showRoute"){
                //draw line from the paine    
                $scope.tourplan = new google.maps.Polyline({
                  path : $scope.flightRoute,
                  strokeColor:"#0000FF",
                  strokeOpacity:0.6,
                  strokeWeight:3
              });              
              $scope.tourplan.setMap($scope.map);
            }                      
          }
          

         //utc time
         setInterval(updateTime, 1000);         
         function updateTime() {
            $scope.currentTime  = new Date();            
            $scope.getTime      = $scope.currentTime.toLocaleString('en-GB', {timeZone: 'Europe/London' }).split(',')[1].split(':');
            $scope.setTime      = $scope.getTime["0"]+':'+$scope.getTime["1"];             
         }  

         
















         

}]);
